/* COLORS */
$naadagreen: #b3ba1c;
$orange: #f7ad00;
$orangeHover: #fabd30;

/* MIXINS */
@mixin downArrow {
  position: absolute;
  bottom: -21px;
  left: 48%;
  @media only screen and (min-width: 375px) and (max-width: 449px) {
    left: 42%; }
  @media only screen and (max-width: 374px) {
    left: 40%; }
  display: inline-block;
  border-radius: 50%;
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  background-color: $naadagreen;
  border-radius: 50%;
  margin-left: 0.75em;
  &:after {
    content: '';
    display: inline-block;
    margin-top: 12px;
    width: 12px;
    height: 12px;
    border-top: 5px solid #fff;
    border-right: 5px solid #fff;
    -moz-transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg); } }

/****/
/** Default DOM Elements Typography **/
/****/

html {
