    color: #fff;
    font-weight: bold;
    background-color: #f7ad00;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px; } }

/* buttons */

@mixin naada-button {
  display: inline-block;
  position: relative;
  margin: 5px;
  /*font-family: Merriweather, 'Open Sans', Arial, sans-serif; */
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px; }

@mixin naada-button-hover {
  display: inline-block;
  position: relative;
  margin: 5px;
  /*font-family: Merriweather, 'Open Sans', Arial, sans-serif; */
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px; }

.naada-button {
  @include naada-button;
  &:hover {
    @include naada-button-hover; } }

.orange-hollow {
  border: 2px solid $orange;
  &:hover {
    color: #fbc13b;
    border: 2px solid #fbc13b; } }

.grey-button {
  color: #fff !important;
  background-color: #3f3f3f;
  &:hover {
    color: #fff !important;
    background-color: #1f1f1f; } }

.green-button {
  color: #fff !important;
  background-color: #b3ba1c;
  &:hover {
    color: #fff !important;
    background-color: #a6ad1b; } }

@mixin orange-button {
  color: #fff !important;
  background-color: #f7ad00; }

.orange-button {
  @include orange-button; }

.footer-widgets a.orange-button, .rev_slider a.orange-button {
  border: 1px solid #fff; }

.orange-button:hover {
  color: #fff !important;
  background-color: #fabd30; }

.inactive-button {
  background-color: #C5C5C5;
  color: #8D8888; }

.naada-button {
  &.small {
    font-size: 11px;
    line-height: 14px;
    padding: 5px 10px; }
  &.medium {
    font-size: 16px;
    line-height: 16px;
    padding: 12px 20px; }
  &.large {
    font-size: 21px;
    line-height: 21px;
    padding: 18px 30px; } }

/* end buttons */

a.button, .button, .entry-content a.button, button, input[type="button"], input[type="reset"], input[type="submit"] {
