      text-align: center; }
    #mc_embed_signup_scroll {
      width: 100%;
      input[type="email"] {
        width: 265px;
        display: inline-block;
        @media only screen and (max-width: 500px) {
          width: 46%; } } } } }


/* MailChimp */

#mc_embed_signup {
