  .genesis-nav-menu {
    #menu-item-329-fr a, #menu-item-4881-en a, #menu-item-329-fr a:hover, #menu-item-4881-en a:hover {
      color: #F7AD00; } } }

.site-header form.search-form {
  margin-top: 21px;
  width: 100%;
  margin-bottom: 0;
  padding: 5px 8px;
  border-radius: 20px;
  font-size: 16px;
  border: 1px solid #ddd;
  float: right;
  height: 30px;
  width: auto;
  @media only screen and (max-width: 700px) {
    margin-top: 60px;
    margin-right: 10px; }
  @media only screen and (max-width: 400px) {
    margin-top: 0; }
  &:hover {
    background-color: $naadagreen;
    color: #fff;
    cursor: text;
    .ico-mglass {
      background: $naadagreen;
      border: 2px solid #fff;
      &::after {
        background: #fff; } } }
  input {
    border: 0;
    border-bottom: 1px solid #ECEFAC;
    display: none;
    background: transparent;
    color: #fff;
    margin-bottom: 0;
    width: 85%;
    padding: 0;
    height: 20px;
    float: right; } }

.ico-mglass {
  position: relative;
  display: inline-block;
  background: #fff;
  border-radius: 30px;
  height: 11px;
  width: 11px;
  border: 2px solid #888;
  float: left;
  margin-top: 3px;

  &:after {
    content: "";
    height: 2px;
    width: 7px;
    background: #888;
    position: absolute;
    top: 7px;
    left: 5px;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg); } }


input[type="search"] {
  &::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #ccc; }
  &:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #ddd;
    opacity: 1; }
  &::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #ddd;
    opacity: 1; }
  &:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #ddd; }
  &:placeholder-shown {
    /* Standard Pseudo-class */
    color: #ddd; } }

section#search-3 {
